<template>
  <div class="addCate">
    <!-- 添加分类 -->
    <ul class="navList">
      <li>
        <label for>
          <input type="text" v-model="navObj.cateName" :placeholder="placeHolder.cateName">
        </label>
      </li>
      <li>
        <label for>
          <input type="text" v-model="navObj.cateTitle" :placeholder="placeHolder.cateTitle">
        </label>
      </li>
      <li>
        <label for>
          <input type="text" v-model="navObj.name" :placeholder="placeHolder.name">
        </label>
      </li>
      <li>
        <label for>
          <input type="text" v-model="navObj.title" :placeholder="placeHolder.title">
        </label>
      </li>
      <li>
        <label for>
          <input type="text" v-model="navObj.href" :placeholder="placeHolder.href">
        </label>
      </li>
      <li>
        <label for>
          <input type="text" v-model="navObj.des" :placeholder="placeHolder.des">
        </label>
      </li>
    </ul>
    <el-button class="addBtn" @click="addCate()">
      <i class="icon iconfont iconanniu-fasong"></i>Add
    </el-button>
  </div>
</template>
<script>
export default {
  name: "AddCate",
  data() {
    return {
      navObj: {
        cateName: "",
        cateTitle: "",
        href: "",
        name: "",
        title: "",
        des: ""
      },
      navList: [],
      placeHolder: {
        cateName: "导航分类",
        cateTitle: "导航分类标题",
        name: "导航标题",
        title: "导航副标题提示",
        href: "导航链接",
        des: "导航描述"
      }
    };
  },
  methods: {
    addCate() {
      let navObj = this.navObj;
      for (let key in this.navObj) {
        if (!this.navObj[key]) {
          this.$message(`请完善${this.placeHolder[key]}`);
          return false;
        }
        if (key != "cateName" || key != "cateTitle") {
          navObj[key] = "";
        }
      }
      this.navList.push(navObj);

      console.log(this.navList);
    }
  }
};
</script>
<style lang="less" scoped>
.addCate {
  .navList {
    width: 80%;
    margin: 0.4rem auto;
    li {
      margin: 0.2rem auto;
    }
    input {
      width: 100%;
      font-size: 0.35rem;
      text-align: center;
      color: tomato;
      height: 0.5rem;
      line-height: 0.5rem;
    }
  }
  .addBtn {
    width: 80%;
    display: block;
    margin: 1% auto;
    line-height: 0.5rem;
  }
}
</style>



