<template>
  <!-- 推荐web站 -->
  <div class="recommendWeb">
    <!-- 标题 -->
    <div class="colors demonstration">{{title}}</div>
    <!-- 红包 -->
    <div class="zfb">{{zfb}}</div>
    <!-- 轮播 -->
    <div class="block">
      <el-carousel height="2.5rem">
        <el-carousel-item
          v-for="(item,index) in imgList"
          :key="index"
          :style="{background:colorBackground(index)}"
        >
          <img
            v-loading="isLoading"
            :element-loading-background="loadingBG"
            element-loading-text="唐唐加载中"
            element-loading-spinner="el-icon-loading"
            :src="item.src"
            :alt="item.title"
          >
        </el-carousel-item>
      </el-carousel>
    </div>
    <p class="colors text-center">正在建设中，敬请期待...</p>
    <!-- 导航链接列表 -->
    <!-- <NavList :isBar="true"></NavList> -->
  </div>
</template>
<script>
export default {
  name: "RecommendWeb",
  data() {
    return {
      isLoading: true, //是否加载
      loadingBG: "rgba(0, 0, 0, 0.8)", //加载背景色
      title: "欢迎您使用唐唐1024团队出品的应用！",
      zfb: "打开支付宝首页搜“659489444”领红包！",
      imgList: [
        {
          src: "./static/image/00.jpg",
          title: "00.jpg"
        },
        {
          src: "./static/image/01.jpg",
          title: "01.jpg"
        },
        {
          src: "./static/image/02.jpg",
          title: "02.jpg"
        },
        {
          src: "./static/image/03.jpg",
          title: "03.jpg"
        },
        {
          src: "./static/image/04.jpg",
          title: "04.jpg"
        },
        {
          src: "./static/image/05.jpg",
          title: "05.jpg"
        },
        {
          src: "./static/image/06.jpg",
          title: "06.jpg"
        },
        {
          src: "./static/image/07.jpg",
          title: "07.jpg"
        },
        {
          src: "./static/image/08.jpg",
          title: "08.jpg"
        }
      ]
    };
  }
};
</script>
<style lang="less" scoped>
@margin: 0.1rem;
@hieght: 0.7rem;
@shadow: 0.05rem;
@alpha: 0.3;
.font(@size:0.3rem) {
  font-size: @size;
}
.recommendWeb {
  padding: @margin;
  // 支付宝红包
  .zfb {
    color: #f6e497;
    .font;
  }
  //   轮播演示
  .demonstration {
    .font;
  }
  .block {
    border-radius: @margin*2;
    box-shadow: 0 0 @shadow #fff, 0 -@shadow #bbab78;

    overflow: hidden;
  }

  /deep/ .el-carousel__container {
    height: 4rem !important;
    img {
      width: 100%;
      height: 100%;
      border-radius: @margin*2;
    }
  }
  /deep/ .el-carousel__indicators {
    width: 100%;
    text-align: center;
  }
}
</style>



