<template>
  <!-- 时间计算器 -->
  <div class="TimeCalculator">
    <div id="diffTime">
      <p class="dayTip">风云变幻，君当自强不息，奋进有加。</p>
      <el-input
        class="targetDate"
        placeholder="输入年份数字2025看效果"
        v-model="targetDate"
        clearable
        @change="changeDate"
      ></el-input>
      <div class="diffTitle">
        <div id="now">{{now}}</div>
        <p>
          距离
          <span id="nextYear">{{nextYear}}</span>年
        </p>
        <div class="targetDays">
          <span id="excessDay">{{excessDay}}</span>天剩余
        </div>
      </div>
      <ul id="timeList">
        <li class="day">
          <div>{{diffDay}}</div>
          <div>天</div>
        </li>
        <li class="hour">
          <div>{{diffHour}}</div>
          <div>时</div>
        </li>
        <li class="min">
          <div>{{diffMin}}</div>
          <div>分</div>
        </li>
        <li class="sec">
          <div>{{diffSec}}</div>
          <div>秒</div>
        </li>
      </ul>
    </div>

    <!-- <svg class="icon shalou" aria-hidden="true">
      <use xlink:href="#iconshalou1"></use>
    </svg>-->
    <!-- 沙漏 -->
    <span class="icon iconfont iconshalou1 shalou"></span>
    <!-- 背景图 -->
    <img id="myBg" src="../../../static/image/bg.png" alt>
  </div>
</template>
<script>
export default {
  name: "timeCalculator",
  data() {
    return {
      targetDate: "", //计算年份
      now: "", //当前时间
      nextYear: "", //下一年
      excessDay: "", //目标剩余时间
      diffDay: "", //目标剩余天数
      diffHour: "", //目标剩余小时
      diffMin: "", //目标剩余分钟
      diffSec: "" //目标剩余秒数
    };
  },
  created() {
    //获取时间
    this.reFreshTime();
  },
  mounted() {
    //周期性更新时间
    let that = this;
    setInterval(function() {
      that.reFreshTime();
    }, 1000);
  },
  methods: {
    reFreshTime() {
      //刷新时间
      let that = this,
        time = new Date(),
        nowSec = time.getTime(), //当前毫秒
        nextSec = Date.parse(
          this.targetDate ? new Date(this.targetDate) : time.getFullYear() + 1
        ), //下一年的毫秒
        diff = (nextSec - nowSec) / 1000; //秒差
      //   console.log(
      //     "now",
      //     time.getTime(),
      //     "next",
      //     Date.parse(time.getFullYear() + 1)
      //   );
      //当前时间
      this.now =
        time.getFullYear() +
        "年" +
        (time.getMonth() < 10
          ? "0" + (time.getMonth() + 1)
          : time.getMonth() + 1) +
        "月" +
        (time.getDate() < 10 ? "0" + time.getDate() : time.getDate()) +
        "日" +
        (time.getHours() < 10 ? "0" + time.getHours() : time.getHours()) +
        "时" +
        (time.getMinutes() < 10 ? "0" + time.getMinutes() : time.getMinutes()) +
        "分" +
        (time.getSeconds() < 10 ? "0" + time.getSeconds() : time.getSeconds()) +
        "秒";
      //下一年
      this.nextYear = this.targetDate
        ? this.targetDate
        : time.getFullYear() + 1;
      this.excessDay = Math.abs(Math.round(diff / (24 * 3600)));

      //距离时间
      this.diffDay = Math.abs(Math.round(diff / (24 * 3600)));
      this.diffHour = Math.abs(Math.round(diff / 3600));
      this.diffMin = Math.abs(Math.round(diff / 60));
      this.diffSec = Math.abs(Math.round(diff));
      //   setInterval(function() {
      //     that.reFreshTime();
      //   }, 1000);
    },
    changeDate(value) {
      //输入日期
      // let rg = /^[1-9]{1,}[\d]*$/;正整数
      let rg = /^[+]{0,1}(\d+)$/, //整数
        val = value ? value : 0;
      if (!rg.test(val)) {
        console.log(val);
        this.$message("请输入年份数字,例:2020");
        this.targetDate = "";
      }
    }
  }
};
</script>
<style lang="less" scoped>
.TimeCalculator {
  width: 100%;
  height: 100%;
  background: linear-gradient(-57deg, #46855d 23%, #1b282e 52%);

  background: -webkit-linear-gradient(-57deg, #46855d 23%, #1b282e 52%);

  background: -moz-linear-gradient(-57deg, #46855d 23%, #1b282e 52%);

  background: -o-linear-gradient(-57deg, #46855d 23%, #1b282e 52%);
}
/* 目标时间样式 */
#diffTime {
  padding-left: 0.2rem;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  height: calc(100% - 1rem);
  .dayTip {
    font-size: 0.3rem;
    color: #92ca94;
    text-align: center;
  }
  .diffTitle {
    color: #fff;
    margin-top: 0.2rem;
  }
  .diffTitle p {
    font-size: 0.4rem;
  }
  .targetDate {
    //输入目标年份
    color: #46855d;
    font-size: 0.3rem;
    margin: 0.1rem 0;
    padding: 0;
    /deep/ .el-input__inner {
      height: 0.6rem;
      line-height: 0.6rem;
      font-size: 0.36rem;
    }
  }
  .targetDays {
    /* 目标天数 */
    font-size: 0.3rem;
    color: #ccc;
  }
}
#now {
  font-size: 0.2rem;
  /* text-align: center; */
  color: #eadcd7;
}
#nextYear {
  font-size: 0.3rem;
  margin: 0 0.1rem;
  color: #eadcd7;
}
#excessDay {
  color: #fff;
  margin-right: 0.1rem;
  font-size: 1rem;
}
/* 时间列表 */
#timeList {
  margin-top: 0.5rem;
  li {
    color: #bbab78;
    margin: 0.2rem 0;
    font-size: 0.3rem;
    div:first-child {
      font-size: 0.8rem;
    }
  }
}
/* 背景图 */
/* 沙漏 */
.shalou {
  position: absolute;
  top: 45%;
  right: 0;
  font-size: 4rem !important;
  color: #f1e4ba;
  transform: translateY(-50%);
}
#myBg {
  position: fixed;
  top: -10%;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 110%;
}
</style>



