<template>
  <!-- 分类链接列表 -->
  <div class="home" :class="themeMetro" :style="{backgroundImage:colorBackground()}">
    <!-- <div class="home"> -->
    <ul class="cateList">
      <li
        draggable="true"
        v-for="(item,index) in cateList"
        :key="index"
        :title="item.title"
        :style="{background:colorBackground(index)}"
        @click="cateJump(item)"
        @dragstart="startDrag($event,index)"
        @dragover.prevent
        @drop="dropDrag($event,index)"
      >
        <i class="icon iconfont" :class="item.icon"></i>
        <!-- {{item.title+(index+1)}} -->
        {{item.title}}
      </li>
    </ul>
    <!-- 设定主题色彩 -->
    <div class="settingModal" v-if="isSetting">
      <div class="settingHead text-center">设定主题色彩</div>
      <div class="settingWrapper">
        <div class="settingContent">
          <el-radio-group v-model="themeMetro" class="metroBtnList" @change="changeMetro">
            <el-radio-button
              v-for="(metro,index) in metroList"
              :key="index"
              :label="metro.label"
            >{{metro.title+'['+metro.tip+']'}}</el-radio-button>
          </el-radio-group>
        </div>
      </div>
      <span class="closeSetting" @click="closeSetting()">
        <i class="icon iconfont iconguanbi"></i>
      </span>
    </div>
    <!-- 视图 -->
    <transition name="bounce" mode="out-in">
      <router-view/>
    </transition>
  </div>
</template>
<script>
export default {
  name: "Home",
  data() {
    return {
      isSetting: false, //是否设置主题
      defaultMetro: "metrox", //默认主题色调
      themeMetro: "", //主题色调
      isDraging: false, //是否drag
      isHover: false, //是否dragover
      // 主题列表
      metroList: [
        {
          label: "metroA1",
          title: "Metro纯色",
          tip: "白框"
        },
        {
          label: "metroA2",
          title: "Metro纯色",
          tip: "黑框"
        },
        {
          label: "metroA3",
          title: "Metro纯色",
          tip: "古典"
        },
        {
          label: "metrox",
          title: "Metro彩色",
          tip: "缤纷"
        },
        {
          label: "metro",
          title: "Metro背景",
          tip: "现代"
        }
      ],
      // 跳转列表
      jumpList: ["love", "recommendweb", "donate", "addcate"],
      // 首页分类
      cateList: [
        {
          title: "推薦",
          cateKey: "recommendWeb",
          icon: "icontuijian"
        },
        {
          title: "影視",
          cateKey: "movie",
          icon: "icondianying"
        },
        {
          title: "音樂",
          cateKey: "music",
          icon: "iconyinyue"
        },
        {
          title: "美圖",
          cateKey: "image",
          icon: "icontupian"
        },
        {
          title: "工具",
          cateKey: "tool",
          icon: "iconAppStore"
        },
        {
          title: "轻应用",
          cateKey: "addCate",
          icon: "iconanniu-fasong"
        },
        {
          title: "打賞",
          icon: "icondashang",
          cateKey: "donate"
        },
        {
          title: "收藏",
          icon: "iconlove",
          cateKey: "Love"
        },
        {
          title: "設定",
          icon: "iconshezhi",
          cateKey: "setting"
        },
        {
          title: "讀書",
          cateKey: "read",
          icon: "iconicon021"
        },
        {
          title: "生活",
          cateKey: "lofter",
          icon: "iconiconset0394"
        }
      ]
    };
  },
  created() {
    // 重置metro主题
    this.resetMetro();
  },
  methods: {
    cateJump(cateItem) {
      // 选择分类
      let jumpName = "NavList"; //默认跳转到导航列表
      if (cateItem.cateKey == "setting") {
        // 当前为设定主题
        this.isSetting = true;
        return false;
      }
      // 筛选跳转指定页面
      if (this.jumpList.indexOf(cateItem.cateKey.toLowerCase()) > -1) {
        let cateKeyArray = cateItem.cateKey.split("");
        cateKeyArray.unshift(cateKeyArray.shift().toUpperCase());
        jumpName = cateKeyArray.join("");
      }
      this.$router.push({
        name: jumpName,
        params: { cateItem }
      });
    },
    startDrag(e, index) {
      //拖拽分类开始
      // console.log(e, index);
      e.dataTransfer.setData("index", index);
      this.isDraging=true;
    },
    dropDrag(e, targetIndex) {
      //放置拖放
      let sourceIndex = e.dataTransfer.getData("index");
      let data = this.cateList.splice(sourceIndex, 1);
      this.cateList.splice(targetIndex, 0, data[0]);
    },
    resetMetro() {
      // 重置主题颜色
      let metro = localStorage.getItem("metro");
      this.themeMetro = metro ? metro : this.defaultMetro;
    },
    changeMetro(val) {
      // 设置主题
      this.themeMetro = val;
      // 重置主题颜色
      localStorage.setItem("metro", val);
    },
    closeSetting() {
      // 关闭主题设置
      this.isSetting = false;
    }
  }
};
</script>
<style lang="less" scoped>
@tabHeight: 2.4rem; //方格高
@tabWidth: 31%; //方格宽
@tabTopMargin: 0.1rem;
@tabLeftMargin: 1%; //方格margin
@color: rgba(255, 80, 30, 0.8);
.baseHeight (@height:@tabHeight) {
  height: @height;
  line-height: @height;
}
//  UI风格定制-(基础版)
.home {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  // height: calc(100% - 1rem);
  height: 100%;
  overflow: auto;
  // 不同UI风格展示分类
  .cateList {
    // 风格1
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    position: relative;

    li {
      // position: absolute;
      overflow: hidden;
      text-transform: capitalize;
      text-overflow: ellipsis;
      white-space: nowrap;
      text-align: center;
      cursor: pointer;
      color: rgb(40, 40, 40);
      width: @tabWidth;
      .baseHeight;
      margin: @tabTopMargin @tabLeftMargin;
      border-radius: 0.1rem;
      box-shadow: 0px 0px 0.15rem rgb(50, 70, 150);
      box-shadow: 0px 0px 0.15rem rgb(10, 10, 20);
      // 风格2
      &:first-child,
      &:nth-child(12n),
      &:nth-child(8n) {
        .baseHeight(@tabHeight*2+ @tabTopMargin*2);
      }
      &:nth-child(8n) {
        float: right;
        width: @tabWidth*2+ @tabLeftMargin*2;
      }
      &:nth-child(9n) {
        position: absolute;
        bottom: 0;
        left: 0;
      }
      &:nth-child(10n) {
        position: absolute;
        top: (@tabHeight+ @tabTopMargin*2);
        left: @tabWidth+ @tabLeftMargin*2;
      }
      &:last-child {
        // float: left;
        position: absolute;
        top: @tabHeight+ @tabTopMargin*2;
        right: @tabLeftMargin;
        .baseHeight;
        width: 31%;
      }
      .icon {
        color: rgba(0, 0, 0, 0.5);
        font-size: 0.5rem;
      }
      &.hover {
        //dragover样式
        background: rgba(21, 161, 131, 0.8)!important;
      }
      &.draging {
        //drag样式
        background: rgba(21, 161, 131, 0.3)!important;
      }
    }
  }
  // UI风格：背景·现代
  &.metro {
    background-image: url(../../../static/image/mb3.jpg) !important;
    background-position: 0 0;
    background-repeat: no-repeat;
    background-size: cover;
    .cateList {
      li {
        color: #000;
        border: 1px solid #fff;
        background: transparent !important;
      }
    }
  }
  // UI风格:纯色·白框
  &.metroA1 {
    .cateList {
      li {
        color: rgba(90, 60, 60, 1);
        border: 1px solid #fff;
        background: transparent !important;
      }
    }
  }
  // UI风格:纯色·黑框
  &.metroA2 {
    .cateList {
      li {
        color: rgba(90, 60, 60, 1);
        background: transparent !important;
      }
    }
  }
  // UI风格:纯色·黑框
  &.metroA3 {
    background-image: linear-gradient(
      127deg,
      rgb(75, 181, 193),
      rgb(255, 111, 102)
    ) !important;
    .cateList {
      li {
        color: rgba(90, 60, 60, 1);
        background: transparent !important;
      }
    }
  }

  //设定主题色彩
  .settingModal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    text-align: left;
    padding: @tabTopMargin;
    .settingHead {
      margin: @tabTopMargin*2;
      font-size: 0.4rem;
      color: @color;
      color: rgba(255, 255, 255, 1);
    }
    .settingWrapper {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 80%;
      transform: translate(-50%, -50%);
    }

    .settingContent {
      font-size: 0.3rem;
      .metroBtnList {
        display: flex;
        justify-content: space-around;
        flex-flow: column wrap;
        align-items: flex-start;
        .el-radio-button {
          margin: @tabTopMargin;
        }
      }
    }
    //关闭设置
    .closeSetting {
      position: absolute;
      top: @tabTopMargin;
      right: @tabTopMargin;
    }
    /deep/ .el-radio-button {
      width: 100%;
      .el-radio-button__inner {
        color: rgb(206, 193, 175);
        border: none;
        font-size: 0.4rem;
        border-radius: @tabTopMargin;
        width: 100%;
      }
    }
    /deep/ .el-radio-button__orig-radio:checked {
      & + .el-radio-button__inner {
        background: @color;
        border-color: @color;
        box-shadow: none;
      }
    }
  }
}
</style>



