import Vue from "vue";
import Router from "vue-router";
// 公用组件
import NavList from "@/components/public/NavList"; //导航列表
import AddCate from "@/components/public/AddCate"; //平台添加导航

// 魔盒应用
import TimeCalculator from "@/components/XBox/TimeCalculator"; //时间计算器
import Colors from "@/components/XBox/Colors"; //色彩页

// 首页组件
import Home from "@/components/homePage/Home"; // 首页
import RecommendWeb from "@/components/homePage/RecommendWeb"; //web推荐
import Love from "@/components/homePage/Love"; //收藏页
import Donate from "@/components/homePage/Donate"; //捐款

// 展示页
import HomePage from "@/views/HomePage"; //首页
import JumpPage from "@/views/JumpPage"; //条装页
import XBox from "@/views/XBox"; //魔盒应用
import Learn from "@/views/Learn"; //学习应用

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/", //默认页
      redirect: "/home"
    },
    {
      path: "/homePage", //主页
      redirect: "/home",
      name: "HomePage",
      component: HomePage,
      children: [
        {
          path: "/home", //首页
          name: "Home",
          component: Home
        },
        {
          path: "/jumpPage",
          name: "JumpPage",
          component: JumpPage,
          children: [
            {
              path: "/navList", //内页导航
              name: "NavList",
              component: NavList
            },
            {
              path: "/love", //收藏
              name: "Love",
              component: Love
            },
            {
              path: "/donate", //捐助
              name: "Donate",
              component: Donate
            },
            {
              path: "/recommendWeb", //web推荐
              name: "RecommendWeb",
              component: RecommendWeb
            },
            {
              path: "/addCate", //平台添加导航
              name: "AddCate",
              component: AddCate
            }
          ]
        }
      ]
    },
    {
      path: "/learn", //学习页
      name: "Learn",
      component: Learn
    },

    {
      path: "/colors", //色彩
      name: "Colors",
      component: Colors
    }
  ]
});



