<template>
  <!-- 其他操作 -->
  <div id="otherBtn">
    <span class="ctrlMusic" v-show="isCanPlay">
      <i class="icon iconfont" :class="isOpen?'iconyinle':'iconguanbiyinle'" @click="toggleMusic()"></i>
      <!-- <audio ref="music" :src="musicSrc" @canplay="canplayMusic()" preload="auto"></audio> -->
      <audio ref="music" :src="musicSrc" loop preload="auto"></audio>
    </span>
    <span class="goTop" @click="goTop()">
      <i class="icon iconfont iconshiliangzhinengduixiang"></i>
    </span>
  </div>
</template>
<script>
export default {
  name: "mdeia",
  props: {
    src: {
      type: String,
      default: ""
    }
  },
  mounted() {
    let that = this;
    that.$refs.music.addEventListener("canplay", function() {
      that.canplayMusic();
    });
  },
  computed: {
    musicSrc() {
      return this.baseURL + this.musicList[this.src];
    }
  },
  data() {
    return {
      isOpen: true, //是否开启音乐
      isCanPlay: false,
      baseURL: "./static/",
      musicList: {
        stronger: "theRose.m4a",
        rose: "WhatDoesn'tKillYou(Stronger)KellyClarkson.mp3"
      }
    };
  },
  methods: {
    goTop() {
      //置顶
      this.$emit("goTop");
    },
    canplayMusic() {
      //音频是否可播放
      this.isCanPlay = true;
      this.$refs.music.play();
    },
    toggleMusic() {
      //切换音乐开关
      this.isOpen = !this.isOpen;
      if (this.isOpen) {
        this.$refs.music.play();
      } else {
        this.$refs.music.pause();
      }
    }
  }
};
</script>
<style lang="less" scoped>
// 其他操作
#otherBtn {
  position: fixed;
  bottom: 1rem;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: stretch;
  flex-flow: column wrap;
  span {
    height: 50%;
    flex-shrink: 1;
    flex-basis: content;
  }
  .iconfont {
    cursor: pointer;
    font-size: 0.6rem;
    color: #fff;
    height: 100%;
    display: flex;
  }
}
</style>



