<template>
  <div class="homePage">
    <!-- 视图 -->
    <transition name="bounce" mode="out-in">
      <router-view/>
    </transition>
    <!-- <a
      target="_blank"
      href="http://wpa.qq.com/msgrd?v=3&amp;uin=1253828761&amp;site=qq&amp;menu=yes"
    >弹出QQ为12345的窗口</a>
    <a-->
    <ul id="optBtn" v-if="isHomeUI">
      <router-link
        tag="li"
        :to="nav.name"
        v-for="(nav,index) in navBtnList"
        :key="index"
        :class="{'router-link-active router-link-exact-active':('/'+nav.name.toLowerCase())==$route.path.toLowerCase()}"
      >
        <a>
          <i class="icon iconfont" :class="nav.icon"></i>
          {{nav.title}}
        </a>
      </router-link>
      <!-- <a href="./good/index.html">斯蒂芬</a> -->
      <!-- <a target="_blank" href="http://wpa.qq.com/msgrd?v=3&amp;uin=1253828761&amp;site=qq&amp;menu=yes">弹出QQ为12345的窗口</a> -->
    </ul>
  </div>
</template>
<script>
export default {
  name: "HomePage",
  data() {
    return {
      isHomeUI: false, //UI风格
      navBtnList: [
        {
          title: "首页",
          icon: "iconshouye",
          name: "Home"
        },

        {
          title: "推荐",
          icon: "icontuijian",
          name: "RecommendWeb"
        },
        // {
        //   title: "学习",
        //   icon: "iconicon021",
        //   name: "Learn"
        // },
        {
          title: "Love",
          icon: "iconshoucang",
          name: "Love"
        },
        //      {
        //   title: "Colors",
        //   icon: "iconshoucang",
        //   name: "Colors"
        // },
        {
          title: "捐赠",
          icon: "iconlove",
          name: "Donate"
        }
      ]
    };
  }
};
</script>
<style lang="less" scoped>
@margin: 0.1rem;
@hieght: 0.7rem;
@shadow: 0.05rem;
.baseHeight (@height:@hieght) {
  height: @height;
  line-height: @height;
}
.homePage {
  height: calc(100% - 1rem);
  // height:100%;
  /* 底部栏 */
  #optBtn {
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: @hieght;
    display: flex;
    justify-content: space-evenly;
    align-content: space-around;
    background: linear-gradient(105deg, #e28585, #4bb5c1);
    li {
      width: 50%;
      border-top: 4px solid #46855d;
      box-shadow: 0 0 @shadow #fff, 0 -@shadow @shadow #bbab78;
      text-align: center;
      cursor: pointer;
      a {
        font-size: 0.25rem;
        display: block;
        .baseHeight;
        color: #fff;
        font-weight: bold;
        white-space: nowrap;
      }
      .icon {
        font-size: 0.6rem;
        color: rgb(240, 206, 183);
        &.iconshoucang {
          font-size: @hieght;
        }
      }
      &.router-link-exact-active {
        a {
          color: rgb(200, 81, 121);
        }
        .icon {
          color: rgb(200, 81, 121);
        }
      }
    }
  }
}
</style>



