<template>
  <div id="app">
    <router-view class="viewPage"/>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {};
  }
};
</script>

<style lang="less" scoped>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow: auto;
  .viewPage {
    // height: calc(100% - 1rem);
    position: relative;
    height: 100%;
    font-size: 0.4rem;
    overflow: hidden auto;
    background: linear-gradient(208deg, rgb(240, 206, 183), rgb(75, 181, 193));
    background: linear-gradient(138deg, rgb(86, 26, 26), rgb(75, 181, 193));
  }
}
</style>



