<template>
  <!-- 导航列表 -->
  <div
    class="navList"
    v-loading.fullscreen.lock="navLoading"
    :element-loading-background="loadingBG"
    element-loading-text="唐唐加载中"
    element-loading-spinner="el-icon-loading"
  >
    <!-- 导航条标题 -->
    <ul class="cateNav" ref="wrapper" :if="isBar">
      <li v-for="(cate,key,index) in cateObjList" :key="key">
        <a
          @click.prevent="goTop('cateName',index)"
          :style="{backgroundImage:colorBackground(index)}"
        >{{cate[0].cateTitle}}</a>
      </li>
    </ul>
    <!-- 导航条目手风琴风格面板 -->
    <div class="navListWrapper" v-for="(cate,key,index) in cateList" :key="key">
      <!-- 导航链接分类标题 -->
      <h2 :style="{backgroundImage:colorBackground(index)}">
        <span>
          <i class="icon iconfont iconleimupinleifenleileibie"></i>
        </span>
        {{cate[0].cateTitle}}
      </h2>
      <!-- 导航链接列表 -->
      <ul ref="cateName">
        <li v-for="(link,index) in cate" :key="index" :style="{background:colorBackground(index)}">
          <el-tooltip class="item" :content="link.title" placement="top-start">
            <a :href="link.href" target="_blank">{{link.name}}</a>
          </el-tooltip>
        </li>
      </ul>
    </div>
    <!-- 多媒体组件 -->
    <Media :src="musicName" @goTop="goTop('wrapper')"></Media>
  </div>
</template>
<script>
//引入多媒体组件
import Media from "@/components/public/Media";

export default {
  name: "NavList",
  components: {
    Media
  },
  props: {
    isBar: {
      type: Boolean,
      default: true
    },
    cateObj: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      navLoading: false, //是否加载
      musicName: "stronger", //背景音乐名
      loadingBG: "rgba(0, 0, 0, 0.8)", //加载背景色
      cateObjList: {},
      // 随机色彩
      wrapperColorList: [
        "#024959",
        "#212b40",
        "#004883",
        "#657786",
        "#341b14",
        "#405952"
      ]
      // itemColorList: [
      //   "#a7c9ae",
      //   "#00abee",
      //   "#00a160",
      //   "#ff6f66",
      //   "#c85179",
      //   "#f0ad4e",
      //   "#ffc0a9",
      //   "#4bb5c1",
      //   "#f2385a",
      //   "#f0ceb7",
      //   "#6cc644",
      //   "#f36454"
      // ]
    };
  },
  created() {
    // if (this.isBar) {
    //   this.getLinkList();
    // }
    let cateJump = sessionStorage.getItem("cateJump");
    this.getLinkList(JSON.parse(cateJump));
  },
  computed: {
    cateList() {
      return Object.keys(this.cateObj).length ? this.cateObj : this.cateObjList;
    },
    wrapperColorName() {
      // 随机色彩
      let classIndex = Math.floor(Math.random() * 10);
      return this.wrapperColorList[classIndex];
      //        let classIndex = Math.ceil((Math.random() * 10 * index) % 10),
      //   deg = Math.round(Math.random() * 300),
      //   startColor = this.itemColorList[Math.round(Math.random() * 10)];
      // return `linear-gradient(${deg}deg,${startColor},${
      //   this.itemColorList[classIndex]
      // })`;
    }
  },
  methods: {
    getLinkList(cateJump) {
      //获取所有链接数据
      let that = this,
        cateList = {
          read: "read",
          movie: "movie"
        },
        cateKey = cateJump && cateJump.cateKey ? cateJump.cateKey : "", //分类
        baseURLON = `static/offlineData/${
          cateList[cateKey] ? cateKey : "navList"
        }.json`, //线上地址
        baseURLOFF = `/navor/static/offlineData/${
         cateList[cateKey] ? cateKey : "navList"
        }.json`, //本地地址
        url = location.origin.match(/localhost/) ? baseURLON : baseURLOFF; //数据地址
      //加载提示
      this.navLoading = true;
      this.$http({
        url: url,
        success(data) {
          if (data.length) {
            //获取当前数据
            let cateObj = {};
            data.forEach(function(item) {
              if (!cateObj.hasOwnProperty(item.cateName)) {
                cateObj[item.cateName] = [];
              }
              cateObj[item.cateName].push(item);
            });

            that.cateObjList = cateObj;
            that.navLoading = false;
          }
          // console.log(" this.navLoading", that.navLoading);
        }
      });
    },
    // colorName(index) {
    //   // 随机色彩
    //   let classIndex = Math.ceil((Math.random() * 10 * index) % 10),
    //     deg = Math.round(Math.random() * 300),
    //     startColor = this.itemColorList[Math.round(Math.random() * 10)];
    //   return `linear-gradient(${deg}deg,${startColor},${
    //     this.itemColorList[classIndex]
    //   })`;
    // },
    goTop(element, index) {
      //置顶
      if (element == "cateName") {
        let index = index ? index - 1 : 0;
        this.$refs[element][index].scrollIntoView();
      } else {
        this.$refs[element].scrollIntoView(false);
      }
    },
    sortChinese(arr, dataLeven) {
      // 参数：arr 排序的数组; dataLeven 数组内的需要比较的元素属性
      /* 获取数组元素内需要比较的值 */
      function getValue(option) {
        // 参数： option 数组元素
        if (!dataLeven) {
          return option;
        }
        var data = option;
        dataLeven.split(".").filter(function(item) {
          data = data[item];
        });
        return data + "";
      }
      arr.sort(function(item1, item2) {
        return getValue(item1).localeCompare(getValue(item2), "zh-CN");
      });
    }
  }
};
</script>
<style lang="less" scoped>
.navList {
  position: relative;
  overflow: auto;
  // 导航条
  .cateNav {
    margin: 0.1rem;
    ul {
      position: relative;

      height: 0.5rem;

      background: rgba(248, 182, 91, 0.3);
    }
    li {
      float: left;
      width: 25%;
      &:hover a {
        z-index: 1;
        color: #f24e4e;
        color: #dee2b9;
        border-radius: 0.2rem 0;
        background: rgba(118, 242, 161, 0.3);
        -webkit-text-fill-color: #333;
      }
      a {
        line-height: 0.5rem;
        font-size: 0.3rem;
        position: relative;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        display: block;

        height: 0.5rem;

        transition: scale, margin-top 0.3s linear;
        text-align: center;
        text-transform: capitalize;
        -webkit-background-clip: text;
        -ms-background-clip: text;
        background-clip: text;
        -webkit-text-fill-color: transparent;
        -ms-fill-color: transparent;
        text-fill-color: transparent;
        -webkit-text-stroke: 1px transparent;
        -ms-text-stroke: 1px transparent;
        text-stroke: 1px transparent;
      }
      a:before,
      a:after {
        position: absolute;
        bottom: -0.1rem;

        display: none;

        width: 0;
        height: 0;

        content: "";

        border: 5px solid;
      }
      a:before {
        left: 0;

        border-color: #fff866 #fff866 transparent transparent;
      }
      a:after {
        right: 0;

        border-color: #fff866 transparent transparent #fff866;
      }
    }
  }
  .navListWrapper {
    clear: both;
    border-top: 2px dashed turquoise;
    // 导航链接列表标题
    h2 {
      font-size: 0.4rem;
      font-weight: normal;
      color: #f36454;
      // display: flex; //与text-fill-color不兼容
      // justify-content: flex-start;//与text-fill-color不兼容
      display: inline-block;
      position: relative;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      -webkit-text-stroke: 1px transparent;
      span {
        margin-right: 0.1rem;
        .icon {
          vertical-align: 0;
        }
      }
    }
    //导航链接列表
    & > ul {
      display: flex;
      padding: 0.4rem 0;
      flex-wrap: wrap;
      justify-content: flex-start;
      & > li {
        display: inline-block;

        box-sizing: border-box;
        width: 47%;
        margin-top: 0.2rem;
        margin-left: 2%;

        transition: width 0.3s linear;

        border-radius: 0.1rem;
        &:hover {
          background: rgba(157, 178, 224, 0.5) !important;
          -webkit-box-shadow: 0 0 2px 5px rgba(157, 178, 224, 0.3);
          -moz-box-shadow: 0 0 2px 5px rgba(157, 178, 224, 0.3);
          box-shadow: 0 0 2px 5px rgba(157, 178, 224, 0.3);
        }
        a {
          font-size: 0.3rem;
          display: block;
          overflow: hidden;

          height: 1rem;
          line-height: 1rem;
          text-align: center;
          white-space: nowrap;
          text-transform: capitalize;
          text-overflow: ellipsis;
          color: rgb(0, 0, 0);
        }
      }
    }
  }
}
@media only screen and(min-width:770px) {
  .navListWrapper {
    & > ul {
      & > li {
        width: 22.5%;
        a {
          height: 0.5rem;
          line-height: 0.5rem;
          font-size: 0.2rem;
        }
      }
    }
  }
}
@media only screen and (max-width: 768px) and(min-width:501px) {
  .navListWrapperWrapper {
    & > ul {
      & > li {
        width: 31%;
      }
    }
  }
}
@media only screen and (max-width: 500px) {
  .navListWrapperWrapper {
    & > ul {
      & > li {
        width: 47%;
      }
    }
  }
}
</style>



