            "此外唐唐还承接：各种切图，响应式手机端开发，微信公众号H5开发，小程序开发，网站Web开发，Vue开发，后台系统等，有需求可以邮箱我 2238506731@qq.com",
          icon: "iconxiaoxi",
          name: "colors"
        },
        {
          title: "您可以为 唐唐 支付您能接受的价格，完全自愿。",
          icon: "iconzhifu",
          name: "colors"
        },
        {
          title: "您可以使用  支付宝  支付捐款",
          icon: "iconzhifubao",
          name: "zfb"
        },
        {
          title: "您可以使用  微信  支付捐款",
          icon: "iconwx-pay",
          name: "wx"
        },
        {
          title: "您可以使用  QQ  支付捐款",
          icon: "iconqq",
          name: "qq"
        }
      ]
    };
  },
  computed: {
    donateLength() {
      return Object.keys(this.nowDonateObj).length;
    }
  },
  methods: {
    clickDonate(item) {
      //切换捐赠
      this.nowSrc = item.src; //图片地址
      this.nowImgTitle = item.imgTitle; //图片提示
      this.downloadName = item.imgTitle; //保存图片文件名
      this.nowTitle = item.payTitle; //付款码提示文字
      this.isDonate = true;
    },
    loaded() {
      //加载图片
      this.isLoading = false;
    },
    saveImg(index) {
      //保存付款码
      this.$refs.downImage[index].click();
    },
    close() {
      //关闭弹框
      this.isDonate = false;
      this.isLoading = true;
    }
  }
};
