}
.donate {
  padding: 0.2rem;
  height: 100%;
  overflow: auto;
  // 信息区
  .header {
    display: flex;
    justify-content: space-around;
    align-items: center;
    // 头像
    .avatar {
      width: 2rem;
      height: 2rem;
      border-radius: 50%;
      display: inline-block;
      .bg;
      background-image: url(../../../static/image/avatar.png) !important;
      background-position: 0 0;
      background-repeat: no-repeat;
      background-size: cover;
    }
    .title {
      width: 60%;
      font-weight: bold;
    }
  }
  //   内容区
  .content {
    // 我的声音
    .myVoice {
