      }
      .zfb {
        color: #00aaee;
      }
    }

    p {
      margin: 0.1rem 0;
      font-size: 0.3rem;
    }
    li {
      margin: 0.1rem 0;
      font-size: 0.3rem;
    }
    // 捐赠header
    .donateHeader {
      color: #007980;
      .bg;
      height: 1rem;
      line-height: 1rem;
      border-radius: 0.1rem;
      .icon {
        margin: 0 0.1rem;
      }
    }
    .donateContent {
      // 按钮组
      .donatBtn {
        margin: 0.1rem 0;
        display: flex;
        flex-flow: column;
        justify-content: flex-start;
        .el-button {
          margin: 0.1rem 0;
          .bg;
          &:hover {
            color: #606266;
          }
        }
        img {
          display: inline-block;
          width: 100%;
        }
      }
    }
  }
  //   捐赠弹框
  // #donateImg {
  //   position: fixed;
  //   top: 0;
  //   left: 0;
  //   z-index: 9999;
  //   width: 100%;
  //   height: 100%;
  //   display: flex;
  //   flex-flow: column;
  //   align-items: center;
  //   justify-content: space-evenly;
  //   background: rgba(20, 50, 75, 0.9);
  //   // .imgContent {
  //   //   position: absolute;
  //   //   top: 50%;
  //   //   left: 50%;
  //   //   width: 100%;
  //   //   height: calc(100% - 1rem);
  //   //   transform: translate(-50%, -50%);
  //   // }
  //   .nowImg {
  //     width: 80%;
  //     margin: 0 auto;
  //     img {
  //       height: 100%;
  //       width: 100%;
  //     }
  //   }
  //   .el-button {
  //     width: 80%;
  //     color: #fff;
  //     background: #ff5722;
  //     border: 0;
  //   }
  //   //关闭按钮
  //   .close {
  //     position: absolute;
  //     top: 0.2rem;
  //     right: 0.2rem;
  //   }
  // }
}
@media only screen and (min-width: 780px) {
  .donateContent {
    // 按钮组
    .btnGroup {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      .donatBtn {
        width: 30%;
      }
    }
  }
}
</style>



