<template>
  <!-- 跳转页 -->
  <div class="jumpPage">
    <div class="commonBtn text-center" ref="commonBtn">
      <i class="icon iconfont iconfanhui" @click="goBack()"></i>
      <span class="title" v-html="jumpTitle"></span>
    </div>
    <!-- 跳转内部页视图 -->
    <transition name="bounce" mode="out-in">
      <router-view class="jumpInnerPage"></router-view>
    </transition>
  </div>
</template>
<script>
export default {
  name: "JumpPage",
  data() {
    return {
      cate: "", //分类
      title: "跳转页提示", //跳转提示文子
      icon: "" //跳转图标
    };
  },
  computed: {
    // 跳转标题
    jumpTitle() {
      return `<i class="icon iconfont ${this.icon}"></i>${this.title}`;
    }
  },
  created() {
    this.resetCate();
  },
  methods: {
    resetCate() {
      //获取sesstionStorage内分类对象

      if (this.$route.params.cateItem) {
        sessionStorage.setItem(
          "cateJump",
          JSON.stringify(this.$route.params.cateItem)
        );
      }
      let cateJump = JSON.parse(sessionStorage.getItem("cateJump"));
      // console.log("cateJump", cateJump);
      this.cate = cateJump.cateKey; //分类
      this.title = cateJump.title; //分类标题
      this.icon = cateJump.icon; //分类图标
    },
    goBack() {
      // 返回上一页
      this.$router.push("/");
    }
  }
};
</script>
<style lang="less" scoped>
@margin: 0.1rem;
@hieght: 0.8rem;
.baseHeight (@height:@hieght) {
  height: @height;
  line-height: @height;
}
.jumpPage {
  padding-top: @hieght;
  // position: absolute;
  // bottom: 0;
  // left: 0;
  // width: 100%;
  height: 100%;
  // 通用按钮
  .commonBtn {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 998;
    width: 100%;
    .baseHeight;
    box-shadow: 0 0.05rem 0.05rem rgb(167, 201, 174);
    background: linear-gradient(124deg, rgb(167, 201, 174), rgb(255, 111, 102));
    .title {
      max-width: 85%;
      font-size: 0.3rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      display: inline-block;
      color: rgb(240, 206, 183);
    }
    // 返回按钮
    .iconfanhui {
      position: absolute;
      top: 50%;
      left: 1%;
      transform: translateY(-50%);
      font-size: 0.5rem;
      height: 100%;
      cursor: pointer;
    }
  }
  //跳转内部页
  .jumpInnerPage {
    height: 100%;
    overflow: auto;
  }
}
</style>



