// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import App from "./App";
import router from "./router";
//引入axios
import axios from "axios";
import qs from "qs";
//引入element-ui
import ElementUI from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
// 引入iconfot
import "../static/iconFont/iconfont.css";
//引入通用less
import "../static/main.less";
import { WSAEDISCON } from "constants";
Vue.use(ElementUI);
Vue.config.productionTip = false;
Vue.prototype.$http = function(param) {
  axios
    .get(param.url, qs.stringify(param.data))
    .then(res => {
      if (res.data.code == 0) {
        param.success(res.data.data);
      } else {
        console.log(res.data.msg);
      }
    })
    .catch(error => {
      console.log(error);
    });
};
let itemColorList = [
    "#a7c9ae",
    "#00abee",
    "#00a160",
    "#ff6f66",
    "#c85179",
    "#f0ad4e",
    "#ffc0a9",
    "#4bb5c1",
    "#f2385a",
    "#f0ceb7",
    "#6cc644",
    "#f36454",
    "#abc8e2"
  ], //颜色集
  jumpURL =
    "https://shop109947244.taobao.com/index.htm?spm=a1z10.1-c-s.w5002-16546784105.2.2b9d1edd02iK4H";
Vue.prototype.colorBackground = function(index) {
  // 随机色彩
  let num = index ? index : 1,
    startIndex = Math.ceil(Math.random() * 10),
    lastIndex = Math.ceil((Math.random() * 10 * num) % 10),
    deg = Math.round(Math.random() * 300);
  // startIndex==lastIndex?
  return `linear-gradient(${deg}deg,${itemColorList[startIndex]},${
    itemColorList[lastIndex]
  })`;
};
