<template>
  <div class="love">
    <!-- 个人信息 -->
    <div class="title" ref="wrapper" :style="{backgroundImage:colorBackground()}">
      <i @click="setUser()" class="icon iconfont iconyonghu"></i>
      <span>{{userName?userName:'火星人'}}</span>
      <i class="left icon iconfont iconbangzhu" @click="toggleTip()"></i>
      <ul class="infoList">
        <li v-for="(item,key) in userInfoList" :key="key">
          <!-- 我的收藏数 -->
          <div class="infoListCount">{{item.count}}</div>
          <!-- 我的收藏标题 -->
          <div class="infoListTitle">{{item.title}}</div>
        </li>
      </ul>
    </div>
    <!-- 标签页 -->
    <el-tabs v-model="activeName" @tab-click="handleClick" type="border-card" stretch>
      <el-tab-pane name="love">
        <span slot="label">
          <i class="icon iconfont iconlove"></i>导航收藏
        </span>
        <!-- 添加导航按钮 -->
        <span class="addLove" :class="isShowBar?'showBar':''" @click="toggleAddBar()">
          <i class="icon iconfont iconicon02" @click="toggleAdd()"></i>
        </span>
        <!-- 导航链接列表 -->
        <NavList :isBar="isBar" :cateObj="cateObjList"></NavList>
      </el-tab-pane>
      <el-tab-pane name="set">
        <span slot="label">
          <i class="icon iconfont iconmianbanbiaoge"></i>导航面板
        </span>
        <!-- 导航面板 -->
        <el-table
          :data="navData"
          style="width: 100%, height:100%"
          stripe
          border
          :default-sort="{prop: 'date', order: 'descending'}"
        >
          <el-table-column prop="cateName" label="分类" sortable></el-table-column>
          <el-table-column prop="cateTitle" label="分类标题" sortable></el-table-column>
          <el-table-column prop="title" label="导航标题" sortable></el-table-column>
          <el-table-column prop="href" label="导航地址" sortable></el-table-column>
          <el-table-column label="操作" fixed="right">
            <template slot-scope="scope">
              <el-button @click="delNav(scope.$index, scope.row)" type="text" size="small">
                <i class="icon iconfont iconshanchu"></i>
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>

    <!-- 设置账户信息 -->
    <div class="tipModal" v-if="isSetUser">
      <div class="tipWrapper">
        <div class="tipHead">设置账户信息</div>
        <div class="tipContent">
          <ul class="infoList">
            <li>
              <label for>
                <input type="text" v-model="userName" placeholder="请输入账户名">
              </label>
            </li>
            <li>
              <label for>
                <input type="text" maxlength="6" v-model="userPwd" placeholder="请输入6位数密码">
              </label>
            </li>
          </ul>
          <div class="setBtn">
            <button @click="cancelUser()">取消</button>
            <button @click="saveUser()">保存</button>
          </div>
        </div>
      </div>
    </div>
    <!-- 添加导航信息 -->
    <div class="tipModal" v-show="isAddNav">
      <div class="tipWrapper">
        <div class="tipHead">添加导航信息</div>
        <div class="tipContent">
          <ul class="navList">
            <li>
              <label for>
                <!-- <input type="text" v-model="navObj.cateName" :placeholder="placeHolder.cateName"> -->
                <el-select
                  v-model="navObj.cateName"
                  :placeholder="placeHolder.cateName"
                  @change="changeCate"
                >
                  <el-option
                    v-for="(item,key) in navCateList"
                    :key="key"
                    :label="item.title"
                    :value="key"
                  ></el-option>
                </el-select>
              </label>
            </li>
            <li>
              <label for>
                <input type="text" v-model="navObj.title" :placeholder="placeHolder.title">
              </label>
            </li>
            <li>
              <label for>
                <input type="text" v-model="navObj.href" :placeholder="placeHolder.href">
              </label>
            </li>
          </ul>
          <div class="setBtn">
            <button @click="closeAdd()">取消</button>
            <button @click="saveNav()">保存</button>
          </div>
        </div>
      </div>
      <span class="closeAdd" @click="closeAdd()">
        <i class="icon iconfont iconguanbi"></i>
      </span>
    </div>
    <!-- 未设置账户的提示信息 -->
    <div class="tipModal" v-show="isTip" @click="toggleTip()">
      <div class="tipWrapper">
        <div class="tipHead">使用须知(关于应用及个人信息)</div>
        <div class="tipContent">
          <ul class="noticeList">
            <li v-for="(notice,index) in noticeList" :key="index">{{notice}}</li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
// 导航链接列表
import NavList from "@/components/public/NavList";
export default {
  name: "love",
  components: {
    NavList
  },
  data() {
    return {
      isBar: false, //是否显示分类标题
      navLoading: false, //是否加载
      musicName: "rose", //背景音乐名
      loadingBG: "rgba(0, 0, 0, 0.8)", //加载背景色
      isSetUser: false, //是否已设账户
      isTip: false, //是否查看帮助
      isShowBar: false, //是否显示添加导航条
      isAddNav: false, //是否添加导航
      userName: "", //用户名
      userPwd: "", //用户密码
      navData: [],
      navObj: {
        cateName: "",
        cateTitle: "",
        title: "",
        href: ""
      },
      defaultMetro: "metrox", //默认主题色调
      // 我的主题
      metroArray: [
        {
          label: "metroA1",
          title: "Metro纯色",
          tip: "白框"
        },
        {
          label: "metroA2",
          title: "Metro纯色",
          tip: "黑框"
        },
        {
          label: "metroA3",
          title: "Metro纯色",
          tip: "古典"
        },
        {
          label: "metrox",
          title: "Metro彩色",
          tip: "缤纷"
        },
        {
          label: "metro",
          title: "Metro背景",
          tip: "现代"
        }
      ],
      // 信息栏
      userInfoList: {
        cate: {
          title: "我的分类",
          count: 0
        },
        theme: {
          title: "我的主题",
          count: "无"
        },
        collect: {
          title: "我的收藏",
          count: 0
        }
      },
      // 导航分类
      navCateList: {
        tool: {
          name: "tool",
          title: "生产力"
        },
        love: {
          name: "love",
          title: "我的爱"
        },
        music: {
          name: "music",
          title: "音乐美声"
        },
        every: {
          name: "every",
          title: "生活日常"
        },
        cool: {
          name: "cool",
          title: "文艺酷站"
        },
        image: {
          name: "image",
          title: "美图美腻"
        },
        movie: {
          name: "movie",
          title: "家庭影院"
        },
        enjoy: {
          name: "enjoy",
          title: "娱乐天地"
        }
      },
      placeHolder: {
        cateName: "请选择导航分类",
        cateTitle: "请输入导航分类标题",
        title: "请输入导航标题",
        href: "请输入导航链接"
      },
      activeName: "love", //激活pane
      noticeList: [
        //使用须知
        "个人信息仅储存于设备本地",
        "清除设备缓存会清空账户及已设导航",
        "使用前请先设置用户名及密码",
        "点击屏幕右侧隐藏栏可添加导航",
        "点击用户名称-保存用户信息",
        "点击导航收藏-查看已设导航",
        "点击导航管理-管理已设导航"
      ],
      myLove: [
        //我的收藏
        {
          cateName: "myLove",
          cateTitle: "我的乖",
          title: "爱情时光机",
          href: "./myLove/index.html"
        }
      ],
      tangtangLove: [
        {
          cateName: "myCommon",
          cateTitle: "网上冲浪",
          title: "AIUNR",
          href: "https://aiunr.com/"
        },
        {
          cateName: "myCommon",
          cateTitle: "超级影视",
          title: "独播库",
          href: "https://www.duboku.com/"
        },
        {
          cateName: "myMovie",
          cateTitle: "超级影视",
          title: "15yc",
          href: "http://www.15yc.com/"
        },
        {
          cateName: "myMovie",
          cateTitle: "超级影视",
          title: "爱看直播",
          href: "http://www.icantv.cn/"
        },
        {
          cateName: "myMovie",
          cateTitle: "超级影视",
          title: "爱看影院",
          href: "https://www.12dvd.com/"
        },
        {
          cateName: "myMovie",
          cateTitle: "超级影视",
          title: "蛋蛋赞影院",
          href: "https://www.dandanzan.com/"
        },
        {
          cateName: "myMovie",
          cateTitle: "超级影视",
          title: "66影视",
          href: "http://www.66s.cc"
        },
        {
          cateName: "myMovie",
          cateTitle: "超级影视",
          title: "人生05",
          href: "http://www.wuhaozhan.net/movie/list/"
        },
        {
          cateName: "images",
          cateTitle: "美图",
          title: "Wallhaven",
          href: "https://alpha.wallhaven.cc/"
        },
        {
          cateName: "images",
          cateTitle: "美图",
          title: "Pixabay",
          href: "https://pixabay.com/"
        },
        {
          cateName: "images",
          cateTitle: "美图",
          title: "500px",
          href: "https://500px.com/"
        },
        {
          cateName: "myCommon",
          cateTitle: "美图",
          title: "浪图",
          href: "https://www.clubajbailey.com/"
        },
        {
          cateName: "images",
          cateTitle: "美图",
          title: "Freebies",
          href: "http://www.freebiesgallery.com/"
        },
        {
          cateName: "images",
          cateTitle: "美图",
          title: "Mobileswall",
          href: "http://www.mobileswall.com/"
        },
        {
          cateName: "images",
          cateTitle: "美图",
          title: "Iphone5wall",
          href: "http://iphone5retinawallpaper.com/"
        },
        {
          cateName: "images",
          cateTitle: "美图",
          title: "Foodpic",
          href: "https://foodiesfeed.com/"
        }
      ]
    };
  },
  created() {
    //初始化
    this.resetUser();
  },
  computed: {
    //分类对象列表
    cateObjList() {
      let cateObj = {},
        tangtangLoveObj = {},
        loveObj = {};
      if (!this.navData.length) {
        return {};
      }
      this.myLove.forEach(function(item) {
        if (!loveObj.hasOwnProperty(item.cateName)) {
          loveObj[item.cateName] = [];
        }
        loveObj[item.cateName].push(item);
      });
      this.tangtangLove.forEach(function(item) {
        if (!tangtangLoveObj.hasOwnProperty(item.cateName)) {
          tangtangLoveObj[item.cateName] = [];
        }
        tangtangLoveObj[item.cateName].push(item);
      });
      this.navData.forEach(function(item) {
        if (!cateObj.hasOwnProperty(item.cateName)) {
          cateObj[item.cateName] = [];
        }
        cateObj[item.cateName].push(item);
      });
      if (this.userName == "唐唐1024" && this.userPwd == "1024") {
        for (let cate in tangtangLoveObj) {
          if (!cateObj.hasOwnProperty(cate)) {
            cateObj[cate] = tangtangLoveObj[cate];
          }
        }
      }
      if (this.userName == "wangke" && this.userPwd == "1024") {
        for (let cate in loveObj) {
          if (!cateObj.hasOwnProperty(cate)) {
            cateObj[cate] = loveObj[cate];
          }
        }
      }
      // console.log(cateObj);
      this.userInfoList.cate.count = Object.keys(cateObj).length; //我的收藏分类数
      let count = 0;
      for (let cate in cateObj) {
        count += cateObj[cate].length;
      }
      this.userInfoList.collect.count = count; //我的收藏导航数

      return cateObj;
    },
    metroList() {
      let metroObj = {};
      this.metroArray.forEach(function(item, index) {
        metroObj[item.label] = item.tip;
      });
      return metroObj;
    }
  },
  methods: {
    resetUser() {
      let user = localStorage.getItem("navUser"), //用户信息
        metro = localStorage.getItem("metro"); //用户主题
      if (user) {
        user = JSON.parse(user);
        this.userName = user.userName;
        this.userPwd = user.userPwd;
        this.navData = user.navData;
        // console.log("nav", user);
      } else {
        this.$message("未设置账户信息，点击右上角“访客”按钮去完善");
      }
      this.userInfoList.theme.count = metro
        ? this.metroList[metro]
        : this.metroList[this.defaultMetro];
    },
    setUser() {
      //设置账户信息
      this.isSetUser = !this.isSetUser;
    },
    cancelUser() {
      //取消保存账户
      this.isSetUser = false;
    },
    saveUser() {
      //保存账户信息
      let user = localStorage.getItem("navUser"),
        info = {
          userName: "",
          userPwd: "",
          navData: []
        };
      if (!this.userName) {
        alert("用户名不可为空");
        return false;
      }
      if (!this.userPwd) {
        alert("密码不可为空");
        return false;
      }
      if (user) {
        user = JSON.parse(user);
        info.navData = user.navData;
      }

      info = {
        userName: this.userName,
        userPwd: this.userPwd,
        navData: []
      };
      localStorage.setItem("navUser", JSON.stringify(info));
      // console.log("user", user);
      this.isSetUser = false;
    },
    saveNav() {
      //保存导航信息
      let user = localStorage.getItem("navUser"),
        navInfo = {};
      if (!user) {
        this.$message("请先设置账户信息");
        this.isAddNav = !this.isAddNav;
        this.isSetUser = !this.isSetUser;
        return fasle;
      }
      for (let key in this.navObj) {
        if (!this.navObj[key]) {
          this.$message(this.placeHolder[key]);
          return false;
        }

        let navVaule = this.navObj[key],
          regCate = new RegExp(/[a-zA-Z]/gi),
          regHref = new RegExp(
            /^((https?|ftp|file):\/\/)?([\da-z\.-]+)\.([a-z\.]{2,6})([\/\w \.-]*)*\/?$/
          );
        if (key == "cateName") {
          if (!regCate.test(navVaule)) {
            this.$message({
              showClose: true,
              message: "分类英文编号仅可输入英文字母",
              type: "error"
            });
            console.log(navVaule);
            return false;
          }
          navVaule = navVaule.toLowerCase();
        }
        if (key == "href" && !regHref.test(navVaule)) {
          this.$message({
            showClose: true,
            message: "请输入正确完整的网址(如https//google.com)",
            type: "error"
          });
          return false;
        }
        navInfo[key] = navVaule;
      }
      user = JSON.parse(user);
      user.navData.push(navInfo);
      this.navData = user.navData;
      localStorage.setItem("navUser", JSON.stringify(user));
      this.isAddNav = !this.isAddNav;
    },
    toggleTip() {
      //切换音乐开关
      this.isTip = !this.isTip;
    },
    toggleAddBar() {
      //切换导航添加条
      this.isShowBar = !this.isShowBar;
    },
    toggleAdd() {
      //切换添加导航
      this.isAddNav = !this.isAddNav;
    },
    closeAdd() {
      //关闭添加导航
      this.isAddNav = false;
    },
    handleClick(tab, event) {
      // console.log(tab, event);
    },
    delNav(index, row) {
      //删除导航
      // console.log("this.navData,row", this.navData, row);
      this.navData.splice(index, 1);
      let info = {
        userName: this.userName,
        userPwd: this.userPwd,
        navData: this.navData
      };
      localStorage.setItem("navUser", JSON.stringify(info));
      // this.cateObjList();
    },
    // 添加导航信息时切换分类下拉框
    changeCate(val) {
      this.navObj.cateTitle = this.navCateList[val].title;
    },
    goTop() {
      //置顶
      this.$refs.wrapper.scrollIntoView(true);
    }
  }
};
</script>
<style lang="less" scoped>
@margin: 0.2rem;
@hieght: 0.7rem;
@shadow: 0.05rem;
@alpha: 0.3;
@font: 0.5rem;
@color: rgba(255, 255, 255, @alpha);
.baseHeight (@height:@hieght) {
  height: @height;
  line-height: @height;
}
.love {
  overflow-x: hidden !important;
  padding: @margin / 2;
  .iconfont {
    margin: 0 @margin;
    font-size: @font;
    &.iconguanbi {
      font-size: @font;
    }
  }
  // 用户信息
  .title {
    // position: absolute;
    // top: 50%;
    // left: 50%;
    // transform: translate(-50%, -50%);
    // z-index: 998;
    position: relative;
    padding: @margin / 2;
    height: @hieght*6;
    border-radius: @margin;
    background: linear-gradient(
      -40deg,
      rgb(242, 56, 90),
      rgb(0, 161, 96),
      rgb(242, 56, 90)
    );
    span {
      color: rgb(100, 60, 50);
      font-size: 0.55rem;
      margin-right: 0.1rem;
    }
    .left {
      float: right;
      font-size: 0.6rem;
      color: #fff;
    }

    .iconyonghu {
      font-size: 2.2rem;
    }
    // 信息列表
    .infoList {
      display: flex;
      justify-content: space-around;
      align-items: center;
      position: absolute;
      bottom: 0.2rem;
      left: 50%;
      width: 100%;
      transform: translatex(-50%);
      li {
        text-align: center;
        font-size: 0.22rem;
        color: rgb(100, 60, 50);
        .infoListCount {
          font-size: 0.5rem;
          color: #fff;
        }
      }
    }
  }
  // 添加收藏
  .addLove {
    position: fixed;
    top: 50%;
    right: -0.8rem;
    z-index: 999;
    width: 1rem;
    padding: @margin;
    transition: all 0.3s linear;
    background: chocolate;
    text-align: center;
    color: #090d23;
    border-radius: @margin / 2 0 0 @margin / 2;
    &.showBar {
      right: 0;
    }
  }
}
// 未设置账户的提示信息
.tipModal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  text-align: left;
  .tipWrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 80%;
    transform: translate(-50%, -50%);
  }
  .tipHead {
    margin: @margin;
    font-size: 0.4rem;
    color: rgba(255, 80, 30, 0.7);
    color: rgba(255, 255, 255, 1);
  }
  .tipContent {
    font-size: 0.3rem;

    // 用户须知信息
    .noticeList {
      li {
        list-style-type: decimal;
        color: #ff5722;
        .baseHeight;
      }
    }
    // 设置个人信息
    .navList,
    .infoList {
      li {
        margin: 0.2rem 0;
        &:first-child {
          padding: 0;
        }
      }

      label {
        display: inline-block;
        width: 100%;
      }
      .el-select {
        width: 100%;
      }
      /deep/ .el-input__inner {
        font-size: 0.3rem;
        .baseHeight;
        padding: 0;
        text-align: center;
        color: #ff5722;
      }
      input {
        .baseHeight;
        font-size: 0.3rem;
        outline: none;
        border: none;
        border-radius: @margin / 2;
        color: #ff5722;
        width: 100%;
        text-align: center;
      }
    }
    //设置个人信息按钮
    .setBtn {
      display: flex;
      justify-content: space-between;
      button {
        width: 48%;
        border-radius: @margin / 2;
        padding: @margin;
        border: none;
        outline: none;
        background: #fff;
      }
    }
  }
  //关闭个人信息设置
  .closeAdd {
    position: absolute;
    top: @margin;
    right: @margin;
  }
}
// elementUI修改
/deep/ .el-tabs--border-card {
  border-color: transparent;
  background: transparent;
  .el-tabs__header {
    background: rgba(20, 50, 70, 0.3);
    // border-color: #13334c;
    overflow: hidden;
    border-color: transparent;
    margin: @margin / 2 0;
    border-radius: @margin;
  }
  .el-tabs__content {
    padding: 0;
  }
  .el-tabs__item {
    color: #de9f98;
    font-size: 0.25rem;
    .baseHeight;
    &:not(.is-disabled):hover {
      color: #fff;
    }
  }
  .el-tabs__item.is-active {
    color: #fff;
    border: none;
    background: #46855d;
  }
}
/deep/ .el-table--border {
  background-color: transparent !important;
  color: #fff;
  tr,
  th,
  td {
    text-align: center;
    background-color: transparent !important;
    color: #fff;
    .icon {
      color: #de9f98;
    }
  }
}
</style>



