<template>
  <div class="learn">
    <!-- 导航条 -->
    <ul class="cateNav" ref="wrapper">
      <li v-for="(cate,key) in cateObjList" :key="key">
        <a :href="'#'+cate[0].cateName">{{cate[0].cateTitle}}</a>
      </li>
    </ul>
    <!-- 导航链接列表-->
    <NavList
      :cateObjList="cateObjList"
      v-loading.fullscreen.lock="navLoading"
      :element-loading-background="loadingBG"
    ></NavList>
    <!-- <div class="navList" v-for="(cate,key) in cateObjList" :key="key">
      <h2 :id="cate[0].cateName">{{cate[0].cateTitle}}</h2>
      <ul>
        <li v-for="(link,index) in cate" :key="index">
          <a :href="link.href" target="_blank">{{link.title}}</a>
        </li>
      </ul>
    </div>-->
    <!-- 其他操作 -->
    <!-- <div id="otherBtn">
      <span class="ctrlMusic">
        <i
          class="icon iconfont"
          :class="isOpen?'iconyinle':'iconguanbiyinle'"
          @click="toggleMusic()"
        ></i>
        <audio ref="music" src="./static/theRose.m4a" loop preload="auto"></audio>
      </span>
      <span class="goTop" @click="goTop()">
        <i class="icon iconfont iconshiliangzhinengduixiang"></i>
      </span>
    </div>-->
    <!-- 多媒体组件 -->
    <Media :src="musicName" @goTop="goTop()"></Media>
  </div>
</template>
<script>
//引入导航列表
import NavList from "@/components/public/NavList";
//引入多媒体组件
import Media from "@/components/public/Media";
export default {
  name: "learn",
  components: { NavList, Media },
  data() {
    return {
      navLoading: false, //是否加载
      musicName: "stronger", //背景音乐名
      loadingBG: "rgba(0, 0, 0, 0.8)", //加载背景色
      cateObjList: {} //链接列表
    };
  },
  created() {
    this.getLinkList();
  },

  methods: {
    getLinkList() {
      //获取所有链接数据
      let that = this;
      this.navLoading = true;
      this.$http({
        url: location.origin.match(/localhost/)
          ? "static/webList.json"
          : "/navor/static/webList.json",
        success(data) {
          if (data.length) {
            //获取当前数据
            let cateObj = {};
            data.forEach(function(item) {
              if (!cateObj.hasOwnProperty(item.cateName)) {
                cateObj[item.cateName] = [];
              }
              cateObj[item.cateName].push(item);
            });
            that.cateObjList = cateObj;
            that.navLoading = false;
          }
          // console.log(" this.navLoading", that.navLoading);
        }
      });
    },
    goTop() {
      //置顶
      this.$refs.wrapper.scrollIntoView(true);
    }
  }
};
</script>
<style lang="less" scoped>
.learn {
  position: relative;
  // 导航条
  .cateNav {
    margin: 0.1rem;
    ul {
      position: relative;

      height: 0.5rem;

      background: rgba(248, 182, 91, 0.3);
    }
    li {
      float: left;
      width: 25%;

      // &:first-child {
      //   width: 19%;
      // }
      &:hover a {
        z-index: 1;

        // margin-top: -0.1rem;
        color: #f24e4e;
        color: #dee2b9;
        border-radius: 0.2rem 0;
        background: rgba(118, 242, 161, 0.3);
      }
      a {
        line-height: 0.5rem;
        font-size: 0.3rem;
        position: relative;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        display: block;

        height: 0.5rem;

        transition: scale, margin-top 0.3s linear;
        text-align: center;
        text-transform: capitalize;
        color: rgb(255, 154, 102);
      }
      a:before,
      a:after {
        position: absolute;
        bottom: -0.1rem;

        display: none;

        width: 0;
        height: 0;

        content: "";

        border: 5px solid;
      }
      a:before {
        left: 0;

        border-color: #fff866 #fff866 transparent transparent;
      }
      a:after {
        right: 0;

        border-color: #fff866 transparent transparent #fff866;
      }
    }
  }
}
</style>



